.main-nav {
  display: inline-block;
  font-size: 0;
  float: right;
  font-size: 1.6rem;
  vertical-align: top;
  width: 20%; }
  .main-nav__list {
    margin-top: 3vh; }
  .main-nav__item {
    display: inline-block;
    text-align: center;
    width: 47%;
    height: 100%;
    text-align: right;
    vertical-align: middle; }
  .main-nav__link {
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    font-size: 1.4rem;
    transition: color 0.2s; }
    .main-nav__link--iconed {
      height: 50px;
      width: auto;
      transition: fill 0.2s; }
      .main-nav__link--iconed:hover {
        fill: #B71C1C; }
    .main-nav__link:hover {
      color: #B71C1C; }

.logo {
  display: inline-block;
  font-family: "Lobster Two", cursive;
  font-size: 50px;
  width: 280px;
  text-align: center;
  line-height: 1.8rem;
  vertical-align: top; }
  .logo__pic {
    float: left; }
  .logo__name {
    display: inline-block;
    margin-top: 25px; }
  .logo__text {
    font-family: "Rajdhani", sans-serif;
    font-size: 1.4rem; }

.main-header {
  position: absolute;
  min-height: 20vh;
  font-size: 0;
  padding-top: 3vh;
  padding-bottom: 3vh;
  padding-left: 5%;
  padding-right: 5%; }
  @media (min-width: 1799px) {
    .main-header {
      padding-left: 13%;
      padding-right: 13%; } }
